@import '../../basic_styles/main.scss';

.catalog {
  &__list {
  display: grid;

  gap: 40px 16px;
  grid-template-columns: repeat(1, 287px);
  justify-content: center;

  margin: 0 auto;


  @include onTablet {
    grid-template-columns: repeat(2, 288px);
  };

  @include onLaptop {
    grid-template-columns: repeat(3, 229px);
  };

  @include onDesktop {
    grid-template-columns: repeat(4, 272px);
  };
}

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 440px;

    border: 1px solid #ccc;
    border-radius: 8px;

    @include onTablet{
      height: 506px;
    };

    @include onLaptop{
      height: 506px;
    }
    
    @include onDesktop{
      height: 506px;
    };
  }


  &__category {
    font-weight: 800;
    font-size: 48px;
    line-height: 56px;
    color: #0F0F11;
    margin-bottom: 8px;
  }

  &__select {
    display: inline-block;

    width: 136px;
    height: 40px;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    margin-right: 16px;
    margin-bottom: 24px;
  }
}
